<script>

export default {
data(){
  return{
    processHeight:400,
    processSpan:10,
    studioCarouselHeight:90,
    projectSpan:5,
    antiShake:true,
    homeCardSpan:5,
    StatisticsSpan:5,
    homeProductionsSpan:10,
