     {path:require('@/assets/img/smart-img/smart.png')},
      {path:require('@/assets/img/work-img/work1.png')},
      {path:require('@/assets/img/work-img/work2.png')},
      {path:require('@/assets/img/work-img/work3.png')},
      {path:require('@/assets/img/work-img/work4.png')},
      {path:require('@/assets/img/process/process-thumb.png')},
      {path:require('@/assets/img/team/team1.png')},
      {path:require('@/assets/img/team/team2.png')},
      {path:require('@/assets/img/team/team4.png')},
      {path:require('@/assets/img/sponsor-img/sponsor1.png')},
      {path:require('@/assets/img/sponsor-img/sponsor2.png')},
      {path:require('@/assets/img/sponsor-img/sponsor3.png')},
      {path:require('@/assets/img/sponsor-img/sponsor4.png')},
      {path:require('@/assets/img/sponsor-img/sponsor5.png')},
      {path:require('@/assets/img/sponsor-img/sponsor6.png')},
    ],
  }
},
methods:{
  prevPage(){
   this.$refs.processRef.prev()
  },
    nextPage(){
   this.$refs.processRef.next()
  },
  listenPage(){
      let a = window.innerWidth
    //  lg >1220
    //  md <=1220
  //  sm <=915
  //  xs <=610
     if(a>915 && a<=1200){
     this.homeCardSpan=8
       this.homeProductionsSpan= 10
       this.StatisticsSpan=8
           this.projectSpan=8
        this.studioCarouselHeight=190
         this.processSpan=10
         this.processHeight=400
     }else if(a>610 && a<=915){
       this.homeCardSpan=10
         this.homeProductionsSpan= 10
           this.StatisticsSpan=10
               this.projectSpan=10
                this.studioCarouselHeight=280
                 this.processSpan=10
                  this.processHeight=400
     }else if(a<=610){
        this.homeCardSpan=24
         this.homeProductionsSpan= 20
           this.StatisticsSpan=20
               this.projectSpan=20
                 this.studioCarouselHeight=280
                 this.processSpan=20
                  this.processHeight=800
     }else{
        this.homeCardSpan=5
          this.homeProductionsSpan= 10
            this.StatisticsSpan=5
            this.projectSpan=5
            this.studioCarouselHeight=90
            this.processSpan=10
             this.processHeight=400
     }

  },
  addClass(e){
     switch (e) {
            case 4:
            this.$refs.playBoxRef4.className = 'play_box'
                break;
                  case 5:
            this.$refs.playBoxRef5.className = 'play_box'
                break;
             
               case 6:
            this.$refs.playBoxRef6.className = 'play_box'
                break;
             
               case 7:
            this.$refs.playBoxRef7.className = 'play_box'
                break;      
        }
  },
   removeClass(e){
      switch (e) {
            case 4:
          this.$refs.playBoxRef4.className = ''
                break;
                  case 5:
            this.$refs.playBoxRef5.className = ''
                break;
             
               case 6:
             this.$refs.playBoxRef6.className = ''
                break;
             
               case 7:
             this.$refs.playBoxRef7.className = ''
                break;      
        }
  
  },
  error(){
  this.dialogVisible=true
  }
},
created(){
  this.listenPage()
  if(localStorage.getItem('people')){
    let num = +localStorage.getItem('people')+1
    this.people = num
  localStorage.setItem('people',num)
  }
  // 
  window.addEventListener('resize',() => {
     this.listenPage()
  });
}
}
</script>

